
body {
  margin: 0;
  padding: 0;
}
#app {
  padding: 0.5em;
  max-width: 800px;
  margin: 0 auto;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #515f6d;
  background: #fafafa;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.6em 0;
  border-bottom: 1px dashed #e6e6e6;
}
li span {
  display: inline-block;
  text-align: center;
  line-height: 1.2em;
  font-size: 0.6rem;
  background: #e2ebff;
  border-radius: 50%;
  width: 1.2em; 
  height: 1.2em;
  padding: 1.2em;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1)
}
input {
  padding: 0.2em;
  margin: 0.5em;
  font-size: 1rem;
}
input[type="submit"],
button {
  color: #515f6d;
  margin: 0.5em;
  padding: 0.3em 1em 0.1em;
  font-size: 1rem;
  text-transform: uppercase;
  background: #e2ebff;
  border: none;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1)
}
.red{
  background: #ff2212;
  color: #060742;
}
.logo{
  position: absolute;
}
.main-nav{
  text-align: right;
}
.width2 {
  width: 7.2em;
}
.modal {
  background: #ffe89d;
  border: 4px solid #f5bd2f;
}
@media(max-width: 800px) {
.logo{
    display: none;
}
}


























































































































































































































































































































































































































































